import Vue from 'vue';
import VueRouter from 'vue-router';
import Mark from 'pages/Mark';
import TaskMark from 'pages/Mark/TaskMark';
import FileAdmin from 'pages/FileAdmin';
import TaskAdmin from 'pages/FileAdmin/TaskAdmin';
import FileUpload from 'pages/FileAdmin/FileUpload';
import FileDownload from 'pages/FileAdmin/FileDownload';
import Administrator from 'pages/Administrator';
import TagAdmin from 'pages/Administrator/TagAdmin';
import UserAdmin from 'pages/Administrator/UserAdmin';
import EntityIteration from 'pages/Administrator/EntityIteration';
import NamevalueIteration from 'pages/Administrator/NamevalueIteration';
import DictExamine from 'pages/Administrator/DictExamine';
import MessageAdmin from 'pages/Administrator/MessageAdmin';
import ExamineMark from 'pages/Administrator/ExamineMark';
import Home from 'pages/Home';
import MyMessage from 'pages/MyMessage';
import Help from 'pages/Help';
import HelpHome from 'pages/Help/HelpHome';
import HelpTaskMark from 'pages/Help/TaskMark';
import HelpTaskAdmin from 'pages/Help/TaskAdmin';
import HelpFileUpload from 'pages/Help/FileUpload';
import HelpFileDownload from 'pages/Help/FileDownload';
import HelpTagAdmin from 'pages/Help/TagAdmin';
import HelpUserAdmin from 'pages/Help/UserAdmin';
import HelpEntityIteration from 'pages/Help/EntityIteration';
import HelpNamevalueIteration from 'pages/Help/NamevalueIteration';
import HelpDictExamine from 'pages/Help/DictExamine';
import HelpMessageAdmin from 'pages/Help/MessageAdmin';

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/home',
    },
    {
      path: '/mark',
      component: Mark,
      children: [
        {
          path: 'taskMark',
          component: TaskMark,
        },
      ],
    },
    {
      path: '/fileAdmin',
      component: FileAdmin,
      children: [
        {
          path: 'taskAdmin',
          component: TaskAdmin,
        },
        {
          path: 'fileUpload',
          component: FileUpload,
        },
        {
          path: 'fileDownload',
          component: FileDownload,
        },
      ],
    },
    {
      path: '/administrator',
      component: Administrator,
      children: [
        {
          path: 'tagAdmin',
          component: TagAdmin,
        },
        {
          path: 'userAdmin',
          component: UserAdmin,
        },
        {
          path: 'entityIteration',
          component: EntityIteration,
        },
        {
          path: 'namevalueIteration',
          component: NamevalueIteration,
        },
        {
          path: 'dictExamine',
          component: DictExamine,
        },
        {
          path: 'messageAdmin',
          component: MessageAdmin,
        },
        {
          path: 'examineMark',
          component: ExamineMark,
        },
      ],
    },
    {
      path: '/home',
      component: Home,
    },
    {
      path: '/myMessage',
      component: MyMessage,
    },
    {
      path: '/help',
      component: Help,
      children: [
        {
          path: 'home',
          component: HelpHome,
        },
        {
          path: 'taskMark',
          component: HelpTaskMark,
        },
        {
          path: 'taskAdmin',
          component: HelpTaskAdmin,
        },
        {
          path: 'fileUpload',
          component: HelpFileUpload,
        },
        {
          path: 'fileDownload',
          component: HelpFileDownload,
        },
        {
          path: 'tagAdmin',
          component: HelpTagAdmin,
        },
        {
          path: 'userAdmin',
          component: HelpUserAdmin,
        },
        {
          path: 'entityIteration',
          component: HelpEntityIteration,
        },
        {
          path: 'namevalueIteration',
          component: HelpNamevalueIteration,
        },
        {
          path: 'dictExamine',
          component: HelpDictExamine,
        },
        {
          path: 'messageAdmin',
          component: HelpMessageAdmin,
        },
      ],
    },
  ],
});
