  <div class="layout">
    <div class="header">
      <div class="title">人机协同表型谱标注系统</div>
      <div class="nav">
        <ul class="nav-list">
          <li
            class="nav-item"
            v-for="item in navList"
            :key="item.text"
            @mouseenter="handleMouseEnter(item)"
            @mouseleave="handleMouseLeave(item)"
          >
            <div
              :class="[
                'nav-text',
                { active: $route.fullPath.indexOf(item.activeTag) > -1 },
              ]"
              @click="!item.children && changeRouter(item)"
            >
              {{ item.text }}
            </div>
            <div class="sub-nav" v-if="item.children" ref="subNav">
              <div
                class="sub-nav-item"
                v-for="subNav in item.children"
                :key="subNav.text"
                @click="changeRouter(subNav, item)"
              >
                {{ subNav.text }}
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="notices">
        <el-dropdown trigger="click">
          <img src="~assets/images/notice.png" alt="" />
          <span class="notice-count">5</span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item divided>
              <div class="dropdown-category">潍坊标注任务</div>
              <div class="dropdown-desc">SAM000000049标注任务审核失败</div>
              <i class="el-icon-time">5分钟</i>
            </el-dropdown-item>
            <el-dropdown-item divided
              ><div class="dropdown-category">标注平台</div>
              <div class="dropdown-desc">您还有400条任务需要标注</div>
              <i class="el-icon-time">10分钟</i></el-dropdown-item
            >
            <el-dropdown-item divided
              ><div class="dropdown-category">标注平台</div>
              <div class="dropdown-desc">您还有400条任务需要标注</div>
              <i class="el-icon-time">2小时</i></el-dropdown-item
            >
            <el-dropdown-item divided
              ><div class="dropdown-category">标注平台</div>
              <div class="dropdown-desc">您还有400条任务需要标注</div>
              <i class="el-icon-time">5小时</i></el-dropdown-item
            >
            <el-dropdown-item divided
              ><div class="dropdown-category">标注平台</div>
              <div class="dropdown-desc">您还有400条任务需要标注</div>
              <i class="el-icon-time">5小时30分钟</i></el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="user">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            <img src="~assets/images/avatar.jpg" alt="" />admin
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="body">
      <page-title :thumbList="thumbList" />
      <router-view />
    </div>
  </div>
</template>

<script>
import PageTitle from 'components/PageTitle';
export default {
  data() {
    return {
      navList: [
        {
          text: '首页',
          path: '/home',
          activeTag: 'home',
        },
        {
          text: '标注',
          activeTag: 'mark',
          children: [
            {
              text: '任务标注',
              path: '/mark/taskMark',
            },
          ],
        },

        {
          text: '管理员',
          activeTag: 'administrator',
          children: [
            {
              text: '实体迭代',
              path: '/administrator/entityIteration',
            },
            {
              text: '名值迭代',
              path: '/administrator/namevalueIteration',
            },
            {
              text: '标签管理',
              path: '/administrator/tagAdmin',
            },
            {
              text: '用户管理',
              path: '/administrator/userAdmin',
            },
            {
              text: '词典审核',
              path: '/administrator/dictExamine',
            },
            {
              text: '消息管理',
              path: '/administrator/messageAdmin',
            },
            {
              text: '审核标注',
              path: '/administrator/examineMark',
            },
          ],
        },
        {
          text: '文件管理',
          activeTag: 'fileAdmin',
          children: [
            {
              text: '文件上传',
              path: '/fileAdmin/fileUpload',
            },
            {
              text: '任务管理',
              path: '/fileAdmin/taskAdmin',
            },
            {
              text: '文件下载',
              path: '/fileAdmin/fileDownload',
            },
          ],
        },
        {
          text: '数据统计',
          path: '/dataStatistic',
        },
        {
          text: '帮助',
          path: '/help',
          activeTag: 'help',
        },
      ],
      thumbList: [],
    };
  },
  computed: {
    hasChildRoutes() {
      return this.navList.filter((item) => item.children);
    },
  },
  mounted() {
    for (let i = 0; i < this.navList.length; i++) {
      if (this.navList[i].children) {
        for (let j = 0; j < this.navList[i].children.length; j++) {
          if (this.navList[i].children[j].path === this.$route.fullPath) {
            this.thumbList = [
              this.navList[i].text,
              this.navList[i].children[j].text,
            ];
            return;
          }
        }
      } else {
        if (this.navList[i].path === this.$route.fullPath) {
          this.thumbList = [this.navList[i].text];
          return;
        }
      }
    }
  },
  methods: {
    handleMouseEnter(activeItem) {
      const index = this.hasChildRoutes.findIndex(
        (item) => item.text === activeItem.text
      );
      if (index > -1) {
        this.$refs.subNav[index].style.display = 'block';
      }
    },
    handleMouseLeave() {
      for (const key in this.$refs.subNav) {
        this.$refs.subNav[key].style.display = 'none';
      }
    },
    changeRouter(activeItem, parentItem) {
      for (const key in this.$refs.subNav) {
        this.$refs.subNav[key].style.display = 'none';
      }
      this.thumbList = [activeItem.text];
      parentItem && this.thumbList.unshift(parentItem.text);
      this.$router.push({
        path: activeItem.path,
      });
    },
  },
  components: {
    PageTitle,
  },
};
</script>

